import createMaterial from "../utils/createMaterial";

const glslify = require("glslify");

const createBasicMaterial = (gl, uniforms, attributes) => createMaterial(gl, glslify(["#define GLSLIFY 1\nconst float PI = 3.14159265359;\n\nattribute vec3 positions;\nattribute vec2 uvs;\nattribute vec3 normals;\n\nuniform mat4 projection;\nuniform mat4 view;\nuniform mat4 model;\nuniform float time;\n\nvarying vec2 texCoord;\nvarying vec2 vUv;\nvarying vec3 vNormal;\nvarying vec3 vPosition;\n\nvoid main() {\n  float angle = time * 0.001 + 10.0 * (uvs.x + uvs.y);\n  vec3 displacement = normals * cos(angle);\n  vUv = uvs;\n  vPosition = positions + displacement;\n  vNormal = vec3(vUv.x, vUv.y, 1.0);\n  texCoord = vec2(1.0, 1.0) * (positions.y/ 5.0);\n  // vec3 displacement = vec3(0.0,1.0, 0.0) * cos(time * 100.0 + uv.x * PI);\n  gl_Position = projection * view * model * vec4(vPosition, 1.0);\n}\n"]), glslify(["precision highp float;\n#define GLSLIFY 1\n\nuniform sampler2D texture;\n\nvarying vec2 vUv;\nvarying vec3 vNormal;\nvarying vec3 vPosition;\nvarying vec2 texCoord;\n\nvoid main() {\n  // const float LOG2 = 1.442695;\n  // const float density = 0.01125;\n  // float z = gl_FragCoord.z / gl_FragCoord.w;\n  // float fogFactor = 1.0 - clamp(exp2(-density * density * z * z * LOG2), 0.0, 1.0);\n\n  // float diff = max(dot(norm,lightDir) * 0.5 + 0.5, 0.0);\n\n  // gl_FragColor = vec4(mix(vNormal.xyz, vec3(1.0,1.0,1.0), fogFactor), 1.0);\n\n  // gl_FragColor = texture2D(texture, vUv);\n\n  gl_FragColor = vec4(vNormal, 1.0);\n  // gl_FragColor = vec4(vUv, 0.0, 1.0);\n  // gl_FragColor = vec4(texCoord, 0.0, 1.0);\n  // gl_FragColor = vec4(1.0,0.0,0.0,0.5);\n}\n"]), uniforms, attributes);

export default createBasicMaterial;